



















































































import { Component, Vue, Watch } from "vue-property-decorator";
import { FlipCard } from "@/store/AllObject";
import { SSR, SR, R, SSRimg, SRimg, Rimg } from "@/store/list";
import Animation from "@/components/Animation.vue";
import { Action, Getter } from "vuex-class";
@Component({
  components: {
    Animation,
  },
})
export default class Flip extends Vue {
  SSRCard = SSR;
  SRCard = SR;
  RCard = R;
  show: boolean = false;
  imageNum: number = 0;
  NameCard: Array<string> = [];
  Victorycard: Array<string> = [];
  ImageCard: Array<string> = [];
  SSRCardImage = SSRimg;
  SRCardImage = SRimg;
  RCardImage = Rimg;

  countTurn: number = 0;
  @Getter disable1!: boolean;
  @Getter disable10!: boolean;
  @Action("getData")
  getData(value: Array<string>) {
    this.getData(value);
  }
  @Action("checkAmulet")
  checkAmulet(val: number) {
    this.checkAmulet(val);
  }
  @Watch("NameCard")
  watchNameCard() {
    this.getData(this.Victorycard);
  }

  cards: Array<FlipCard> = [
    {
      id: 1,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 2,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 3,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 4,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 5,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 6,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 7,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 8,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 9,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
    {
      id: 10,
      front: 'The "First Computer Programmer"',
      img: "",
      back: "",
      flipped: false,
      color: "black",
      type: "Random",
    },
  ];
  toggle1Card(val: number) {
    this.NameCard = [];
    this.cards.map((c) => {
      if (c.id == val && !c.flipped) {
        this.checkAmulet(1);
        if (!this.disable1) {
          this.countTurn++;
          c.flipped = true;
          let x = this.rnd(0, 100);
          if (x == 1) {
            let num: number = this.rnd(0, this.SSRCard.length - 1);
            c.back = this.SSRCard[num];
            c.img = this.SSRCardImage[num];
            this.imageNum = num;

            this.NameCard.push(this.SSRCard[num]);
            this.Victorycard.push(this.SSRCard[num]);
            this.show = !this.show;
            setTimeout(() => {
              this.show = false;
            }, 1500);
            if (num <= 21) {
              c.color = "amber lighten-1";
              c.type = "SSR";
            } else {
              c.color = "pink accent-3";
              c.type = "SP";
            }
          } else if (2 <= x && x <= 27) {
            let num: number = this.rnd(0, this.SRCard.length - 1);
            c.back = this.SRCard[num];
            c.img = this.SRCardImage[num];
            c.color = "deep-purple lighten-1";
            c.type = "SR";
          } else {
            let num: number = this.rnd(0, this.RCard.length - 1);
            c.back = this.RCard[num];
            c.img = this.RCardImage[num];

            c.color = "blue darken-1";
            c.type = "R";
          }
        }
      }
    });
  }
  toggleCard() {
    this.checkAmulet(10 - this.countTurn);
    if (!this.disable10) {
      // console.log(this.disable10);
      this.countTurn = 0;
      this.NameCard = [];
      this.cards.map((c) => {
        if (!c.flipped) {
          c.flipped = true;
          let x = this.rnd(0, 100);
          if (x == 1) {
            let num: number = this.rnd(0, this.SSRCard.length - 1);
            c.back = this.SSRCard[num];
            c.img = this.SSRCardImage[num];
            this.NameCard.push(this.SSRCard[num]);
            this.Victorycard.push(this.SSRCard[num]);
            this.imageNum = num;
            // console.log(this.imageNum);
            this.show = !this.show;
            setTimeout(() => {
              this.show = false;
            }, 1500);
            if (num <= 21) {
              c.color = "amber lighten-1";
              c.type = "SSR";
            } else {
              c.color = "pink accent-3";
              c.type = "SP";
            }
          } else if (2 <= x && x <= 27) {
            let num: number = this.rnd(0, this.SRCard.length - 1);
            c.back = this.SRCard[num];
            c.img = this.SRCardImage[num];
            c.color = "deep-purple lighten-1";
            c.type = "SR";
          } else {
            let num: number = this.rnd(0, this.RCard.length - 1);
            c.back = this.RCard[num];
            c.img = this.RCardImage[num];

            c.color = "blue darken-1";
            c.type = "R";
          }
        }
      });
    }
  }
  rnd(a: number, b: number) {
    return Math.floor((b - a + 1) * Math.random()) + a;
  }
  reset() {
    this.countTurn = 0;
    this.cards.map((c) => {
      c.flipped = false;
      c.color = "black";
    });
  }
  showDetail() {
    alert("Tính năng đang hoàn thiện");
  }
}
